body {
  display: flex;
  justify-content: center;
  align-items: center;
}

.app {
  display: flex;
  padding: 30px 0;
}

.game {
  width: 560px;
  height: 560px;
  display: flex;
  flex-wrap: wrap;
}

.game img {
  width: 70px;
  height: 70px;
}

.links {
  text-align: center;
  font-size: 1.25rem;
}

.links a {
  text-decoration: none;
  color: blue;
}

.links a:hover,
.links a:focus {
  color: orangered;
}
